
import { AjaxHttp } from '../../../js/ajaxHttp'
import { getYMDHMFormateTimestamp, isArrayType } from "../../../js/utility.js"
import Selector from 'vux/src/components/selector/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'

import event from '../../../js/Event.js'
import PlateType from '../../../js/enum.js'
import { mapState } from 'vuex'

export default {
name: 'createschedule',
components: {
Selector,
XInput,
Datetime,
Checker
},
computed: {
PlateType() {
return PlateType;
},
...mapState([
'detail',
'scheduleMain'
])
},
data() {
return {
content: '',
starttime: '',
endtime: '',
typedata: [{ 'key': 'customer', 'value': '客户' }, { 'key': 'contacts', 'value': '联系人' }, { 'key': 'leads', 'value': '线索' }, { 'key': 'business', 'value': '商机' }, { 'key': 'product', 'value': '产品' }],
module: '',
module_id: '',
module_name: '',
description: '',
colordata: ['#62a8ea', '#46be8a', '#57c7d4', '#f2a654', '#f96868', '#526069', '#926dde'],
colorindex: -1,
}
},
mounted() {
event.$on('saveDone', function() {
this.saveInfos();
}.bind(this));
if (this.detail !== '' && this.detail.type === PlateType.Schedule) {
console.log(this.detail);
this.$nextTick(function() {
this.content = this.detail.content;
this.starttime = this.detail.starttime;
this.endtime = this.detail.endtime;
this.module = this.detail.module;
this.module_id = this.detail.module_id;
this.module_name = this.detail.module_name;
this.description = this.detail.description;
this.colorindex = this.detail.colorindex;
})
}
},
beforeDestroy() {
event.$off("saveDone");
},
beforeRouteLeave(to, from, next) {
if (to.name === 'relatelist') {
this.$store.commit('updateDetail', {
type: PlateType.Schedule,
content: this.content,
starttime: this.starttime,
endtime: this.endtime,
module: this.module,
module_id: this.module_id,
module_name: this.module_name,
description: this.description,
colorindex: this.colorindex,
});

event.$on('selectedRelateItem', function(data) {
this.handleSelectedInfo(data);
}.bind(this));
} else if (to.name === 'schedule') {
this.$store.commit('updateDetail', '');
}
next();
},
beforeRouteEnter(to, from, next) {
if (from.name === 'relatelist') {
event.$off("selectedRelateItem");
}
next();
},
methods: {
saveInfos() {
if (this.content === '') {
this.$vux.toast.text("请输入内容", 'top');
return;
} else if (this.starttime === '') {
this.$vux.toast.text("请选择开始时间", 'top');
return;
} else if (this.endtime === '') {
this.$vux.toast.text("请选择结束时间", 'top');
return;
}

var url = 'm=event&a=add';
var postParams = {
subject: this.content,
start_date: getYMDHMFormateTimestamp(this.starttime),
end_date: getYMDHMFormateTimestamp(this.endtime),
module: this.module,
module_id: this.module_id,
color: this.colorindex !== -1 ? this.colordata[this.colorindex] : '',
description: this.description,
};
if (this.$route.params.action === 'edit') {
url = 'm=event&a=edit';
postParams.id = this.detail.id;
}

AjaxHttp('POST', url, postParams, this, function(res, wself, success) {
if (success) {
wself.scheduleMain.refresh = true;
wself.$store.commit('updateScheduleMain', wself.scheduleMain);
wself.$vux.toast.text(res.info, 'top');
wself.$router.go(-1);
}
});
},
onChange(val) {
if (this.module !== val) {
this.module_id = '';
this.module_name = '';
}
},
onRelateClick() {
if (this.module === '') {
this.$vux.toast.text("请先选择相关类型", 'top');
return;
}

var plateType = PlateType.Default;
if (this.module === 'customer') {
plateType = PlateType.Customer;
} else if (this.module === 'contacts') {
plateType = PlateType.Contacts;
} else if (this.module === 'leads') {
plateType = PlateType.Leads;
} else if (this.module === 'business') {
plateType = PlateType.Business;
} else if (this.module === 'product') {
plateType = PlateType.Product;
}

this.$router.push({
name: 'relatelist',
params: {
type: plateType,
fromtype: PlateType.Schedule,
action: 'select',
section: '1',
row: '3',
addition: '0'
}
});
},
colorSelect(idx) {
if (this.colorindex !== idx) {
this.colorindex = idx;
} else {
this.colorindex = -1;
}
},
handleSelectedInfo(data) { //相关列表数据处理
this.detail.module_id = data.item.id;
this.detail.module_name = data.item.title;
this.$store.commit('updateDetail', this.detail);
},
}
}




